<template>
  <div class="my-list" :class="classes">
    <!---->
    <div v-if="$slots.header"
         ref="header"
         class="my-list__header"
         :style="headerStyle">
      <slot name="header"></slot>
    </div>

    <!--  -->
    <div ref="viewport"
         class="my-list__wrapper"
         @scroll="handleVirtualScroll"
         :class="wrapperClass"
         :style="wrapperStyle">
      <!--  -->
      <MySpin v-bind="spin" :fit="isFit" :loading="(isPager || isVirtual) && currentLoading">

        <!--  -->
        <div v-if="!list.length && !errorMessage" class="my-list__empty">
          <slot name="empty">
            
          </slot>
        </div>

        <div v-if="error && errorMessage" class="my-list__error">
          <slot name="error" :message="errorMessage">
            
          </slot>
        </div>

        <!--  -->
        <div v-if="isVirtual" class="my-list__placeholder" :style="{height:`${contentHeight}px`}"></div>

        <ul ref="list"
            class="my-list__content"
            v-if="list.length && !errorMessage"
            v-infinite-scroll="load"
            :infinite-scroll-immediate="auto"
            :infinite-scroll-delay="scrollDelay"
            :infinite-scroll-distance="scrollDistance"
            :infinite-scroll-disabled="scrollDisabled"
            :style="contentStyle">
          <li v-for="(item, index) in (isVirtual ? viewItems : list)"
              :key="`item_${index}`"
              class="my-list__item"
              :class="createItemClass(index)"
              :style="itemStyle">
            <slot :item="item"
                  :index="startIndex + index"
                  :page="currentPage"
                  :pageSize="currentPageSize"
                  :total="currentTotal">
              {{item}}
            </slot>
          </li>
          <li v-for="n in fixItemCount"
              :key="n"
              class="my-list__item"
              :class="createItemClass((isVirtual ? viewItems : list).length + n - 1)"
              :style="itemStyle"></li>
        </ul>

        <!--  -->
        <div v-if="isScroll && !isNoMore && currentLoading" class="my-list__scroll-loading">
          <slot name="skeleton">
            <MySpin loading></MySpin>
          </slot>
        </div>

        <!--  -->
        <div v-if="isScroll && isNoMore && currentPage>1" class="my-list__complete">
          <slot name="complete">
            
          </slot>
        </div>


      </MySpin>
    </div>

    <!--  -->
    <div v-if="$slots.footer"
         ref="footer"
         class="my-list__footer"
         :style="footerStyle">
      <slot name="footer"></slot>
    </div>

    <!--  -->
    <Pagination v-if="isPager && currentTotal"
                ref="pager"
                v-bind="pagerProps"
                :total="currentTotal"
                :currentPage="currentPage"
                :class="pagerClass"
                @size-change="handlePageSizeChange"
                @current-change="handlePageChange"></Pagination>
  </div>

</template>

<script>

  /**
   * 
   * @module $ui/components/my-list
   */

  import {Pagination} from 'element-ui'
  import {MySpin} from '$ui'

  import Style from './Style'
  import Pager from './Pager'
  import Scroll from './Scroll'
  import Virtual from './Virtual'

  /**
   * 
   * @member slots
   * @property {string} default item  index , page pageSize total 
   * @property {string} header 
   * @property {string} footer 
   * @property {string} skeleton 
   * @property {string} empty 
   * @property {string} error errortrue
   * @property {string} complete 
   */
  export default {
    name: 'MyList',
    mixins: [Style, Pager, Scroll, Virtual],
    components: {
      Pagination,
      MySpin
    },

    /**
     * 
     * @member props
     * @property {Array} [data] 
     * @property {Function} [loader] page pageSize Promise{list, total}
     * @property {Number|Object} , {xxl,xl,lg,md,sm,xs}
     * @property {boolean} [fixColumns]  columns > 1 
     * @property {boolean} [border] 
     * @property {boolean} [split] 
     * @property {boolean} [stripe] 
     * @property {boolean} [size] ,  'large', 'small', 'mini', ''
     * @property {Object} [headerStyle] 
     * @property {Object} [footerStyle] 
     * @property {Object} [spin]  {tip, size}MySpin
     * @property {Boolean} [loading]  
     * @property {string} [mode=pager] 'pager', 'scroll', 'virtual'
     * @property {number} [page=1]  1
     * @property {number} [pageSize=10] 
     * @property {number} [total=0] 
     * @property {Object} [pagination] ,  {layout, background, small}ElPagination
     * @property {boolean} [auto=true] loader
     * @property {boolean} [error] 
     * @property {number} [scrollDelay=200] ms, 
     * @property {number} [scrollDistance=0] px, 
     * @property {object} [skeleton] 
     * @property {boolean} [fit] true
     * @property {number} [itemHeight] 
     *
     */
    props: {
      // 
      data: {
        type: Array,
        default() {
          return []
        }
      },
      // 
      loader: {
        type: Function
      },
      // 
      columns: {
        type: [Number, Object],
        default: 1
      },
      //  columns > 1 
      fixColumns: Boolean,
      // 
      border: Boolean,
      // 
      split: Boolean,
      // 
      stripe: Boolean,
      // 
      size: {
        type: String,
        default: '',
        validator(val) {
          return ['large', 'small', 'mini', ''].includes(val)
        }
      },
      // 
      headerStyle: Object,
      // 
      footerStyle: Object,

      // loading
      spin: {
        type: Object,
        default() {
          return {
            tip: '...',
            size: 'large'
          }
        }
      },

      //  
      loading: Boolean,

      // 
      mode: {
        type: String,
        default: 'pager',
        validator(val) {
          return ['pager', 'scroll', 'virtual'].includes(val)
        }
      },

      // , 1
      page: {
        type: Number,
        default: 1
      },

      // 
      pageSize: {
        type: Number,
        default: 10
      },

      // 
      total: {
        type: Number,
        default: 0
      },

      // ,  layout, background, small
      pagination: {
        type: Object,
        default() {
          return {}
        }
      },
      // loader
      auto: {
        type: Boolean,
        default: true
      },
      // 
      error: {
        type: Boolean
      },
      // ms
      scrollDelay: {
        type: Number,
        default: 200
      },
      // px
      scrollDistance: {
        type: Number,
        default: 0
      },
      // 
      skeleton: {
        type: Object,
        default() {
          return {
            active: true,
            title: true,
            paragraph: {
              rows: 1,
              width: '100%'
            }
          }
        }
      },
      // true
      fit: Boolean,

      // 
      itemHeight: Number
    },
    data() {
      return {
        list: [],
        currentPage: this.page,
        currentTotal: this.total,
        currentPageSize: this.pageSize,
        currentLoading: false,
        errorMessage: null
      }
    },
    computed: {
      pageCount() {
        return Math.ceil(this.currentTotal / this.currentPageSize)
      }
    },
    watch: {
      // 
      data: {
        immediate: true,
        handler(val) {
          this.list = Object.freeze(val.slice(0))
        }
      },

      list() {
        //  virtual 
        if (!this.isVirtual) return
        this.$nextTick(() => {
          this.scrollTop()
        })
      },
      pagerProps: {
        immediate: true,
        handler(props) {
          this.currentPage = props.currentPage
          this.currentTotal = props.total
          this.currentPageSize = props.pageSize
          this.auto && this.$nextTick(this.load)
        }
      }
    },
    methods: {
      /**
       * loader
       * @method load
       */
      load() {
        if (!this.loader) return

        const loadedMap = {
          pager: this.pagerLoaded,
          scroll: this.scrollLoaded,
          virtual: this.virtualLoaded
        }

        this.currentLoading = true
        this.errorMessage = null
        this.loader(this.currentPage, this.currentPageSize).then(res => {
          const loaded = loadedMap[this.mode]
          loaded && loaded(res)
          this.currentTotal = res.total || 0
          /**
           * 
           * @event success
           * @param {object} [res] 
           */
          this.$emit('success', res)
        }).catch(e => {
          this.errorMessage = e;
          /**
           * 
           * @event error
           * @param {*} e 
           */
          this.$emit('error', e)
        }).finally(() => {
          this.currentLoading = false
        })
      },
      /**
       * 
       * @method refresh
       * @param {number} [page=1] 
       */
      refresh(page) {
        this.currentPage = page || this.currentPage
        this.load()
      },
      /**
       * fit=true  mode=virtual
       * @method scrollTop
       */
      scrollTop(val = 0) {
        if (this.$refs.viewport) {
          this.$refs.viewport.scrollTop = val
        }
      },
      /**
       * 
       * @method reset
       */
      reset() {
        this.scrollTop(this.startOffset)
      }
    }
  }
</script>
