<template>
  <MyBase v-on="$listeners"
          v-bind="$props"
          tag="div"
          class="my-paragraph"
          :children="$slots.default">
  </MyBase>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-paragraph
   */
  import Base from './Base'
  /**
   * 
   * @member slots
   * @property {string} default  
   */

  /**
   * 
   * @event copy
   * @param {Object} 
   */

  /**
   * 
   * @event editing
   */

  /**
   * 
   * @event expand
   * @param {Boolean} expanded 
   */

  /**
   * 
   * @event edited
   * @param {String} text 
   */

  /**
   * 
   * @event change
   * @param {String} text 
   */

  export default {
    name: 'MyParagraph',
    components: {
      MyBase: Base
    },
    /**
     * 
     * @member props
     * @property {Boolean} [copyable=false] 
     * @property {String} [copyText] , DOMinnerText
     * @property {Boolean} [delete=false] 
     * @property {Boolean} [disabled=false] 
     * @property {Boolean} [editable=false] 
     * @property {Boolean} [ellipsis=false] 
     * @property {Number} [rows=1]  ellipsis true 
     * @property {Boolean} [expandable=false]  ellipsis true 
     * @property {Boolean} [mark=false] 
     * @property {Boolean} [underline=false] 
     * @property {Boolean} [code=false] 
     * @property {Boolean} [strong=false] 
     * @property {Boolean} [pre=false] 
     * @property {String} [type]  '', 'primary', 'secondary', 'warning', 'danger'
     */
    props: {
      ...Base.props
    }
  }
</script>

