<template>
  <div class="my-description" :class="{'is-top':top,'is-inline':inline}">
    <div
      class="my-description__title"
      :style="titleStyle"
      :class="titleClass">
      <slot name="title">{{title}}</slot>
    </div>
    <div class="my-description__content"
         :class="contentClass"
         :style="contentStyle">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  /**
   * 
   * @module $ui/components/my-description
   */

  /**
   *  slots
   * @member slots
   * @property {string} default 
   * @property {string} title 
   */
  export default {
    name: 'MyDescription',
    /**
     * 
     * @member props
     * @property {string} [title] 
     * @property {number} [width]  top false
     * @property {number} [gutter=10]  
     * @property {string} [align=left] ,  'left', 'right', 'center'
     * @property {boolean} [top=false] 
     * @property {boolean} [inline=false] 
     */
    props: {
      // 
      title: String,
      //  top false
      width: Number,
      // 
      gutter: {
        type: Number,
        default: 10
      },
      // 
      align: {
        type: String,
        default: 'left',
        validator(val) {
          return ['left', 'right', 'center'].includes(val)
        }
      },
      // 
      top: Boolean,
      // 
      inline: Boolean
    },
    computed: {
      hasTitle() {
        return this.title || this.$slots.title
      },
      titleStyle() {
        return {
          width: this.top ? null : `${this.width}px`
        }
      },
      contentStyle() {
        return {
          display: (this.top || this.width) ? 'block' : 'inline',
          paddingLeft: (this.hasTitle && !this.top)
            ? `${(this.width || 0) + this.gutter}px`
            : null,
          paddingTop: (this.top && this.hasTitle) ? `${this.gutter}px` : null
        }
      },
      titleClass() {
        return {
          [`is-${this.align}`]: !!this.align
        }
      },
      contentClass() {
        return {
          [`is-${this.align}`]: (this.top && !!this.align)
        }
      }
    }
  }
</script>

